
import MessageComponent from "./MessageComponent.vue";

export default {
  name: "BurgerForm",
  data() {
    return {
      breads: [],
      patties: [],
      optionalData: [],
      name: "",
      bread: "",
      patty: "",
      optional: [],
      msg: "",
    };
  },
  methods: {
    async getIngredients() {
      const req = await fetch(
        "https://burger-api-odgl.onrender.com/ingredientes"
      );
      const data = await req.json();
      this.breads = data.paes;
      this.patties = data.carnes;
      this.optionalData = data.opcionais;
    },
    async createBurger(e: Event) {
      e.preventDefault();

      const data = {
        nome: this.name,
        carne: this.patty,
        pao: this.bread,
        opcionais: Array.from(this.optional),
        status: "Solicitado",
      };
      const dataJson = JSON.stringify(data);
      const req = await fetch("https://burger-api-odgl.onrender.com/burgers", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: dataJson,
      });

      const res = await req.json();
      this.msg = `Pedido Nº ${res.id} feito com sucesso!`;
      setTimeout(() => (this.msg = ""), 3000);

      this.name = "";
      this.patty = "";
      this.bread = "";
      this.optional = [];
    },
  },
  mounted() {
    this.getIngredients();
  },
  components: { MessageComponent },
};
